.App {
  min-height: 100vh;
}

header {
  display: flex;
  justify-content: center;
  padding: 24px 16px 16px;
  width: 100%;
}

header label {
  display: flex;
  align-items: center;
  position: relative;
  width: 100%;
  max-width: 560px;
}

header .search-icon {
  position: absolute;
  left: 14px;
  width: 18px;
  height: 18px;
  color: #888;
  pointer-events: none;
}

header input {
  width: 100%;
  padding: 12px 40px 12px 44px;
  font-size: 16px;
  border: 1px solid #333;
  border-radius: 12px;
  background: #1e1e1e;
  color: #e0e0e0;
  outline: none;
  transition: border-color 0.2s, box-shadow 0.2s;
}

header input::placeholder {
  color: #666;
}

header input:focus {
  border-color: #555;
  box-shadow: 0 0 0 3px rgba(255, 255, 255, 0.06);
}

header label #clear {
  position: absolute;
  right: 10px;
  width: 22px;
  height: 22px;
  padding: 0;
  border: none;
  border-radius: 50%;
  background: #444;
  color: #aaa;
  font-size: 14px;
  line-height: 22px;
  text-align: center;
  cursor: pointer;
  opacity: 0.6;
  transition: opacity 0.15s, background 0.15s;
}

header label #clear:after {
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  display: inline-block;
  line-height: 22px;
  content: "\00d7";
}

header label #clear:hover,
header label #clear:focus {
  opacity: 1;
  background: #555;
}

.meme-card {
  border-radius: 10px;
  overflow: hidden;
  background: #1e1e1e;
  transition: transform 0.2s ease, box-shadow 0.2s ease;
  cursor: pointer;
}

.meme-card:hover {
  transform: scale(1.03);
  box-shadow: 0 8px 24px rgba(0, 0, 0, 0.5);
}

.meme-card img {
  display: block;
  width: 100%;
}

.loading {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 48px 16px;
  color: #888;
}

.loading p {
  margin-top: 16px;
  font-size: 14px;
}

.spinner {
  width: 32px;
  height: 32px;
  border: 3px solid #333;
  border-top-color: #888;
  border-radius: 50%;
  animation: spin 0.8s linear infinite;
}

@keyframes spin {
  to { transform: rotate(360deg); }
}

.suggestions {
  position: absolute;
  top: 100%;
  left: 0;
  right: 0;
  margin: 4px 0 0;
  padding: 4px 0;
  list-style: none;
  background: #1e1e1e;
  border: 1px solid #333;
  border-radius: 10px;
  z-index: 100;
  overflow: hidden;
}

.suggestions li {
  padding: 10px 16px;
  color: #e0e0e0;
  font-size: 15px;
  cursor: pointer;
}

.suggestions li:hover,
.suggestions li.active {
  background: #2a2a2a;
}

.hidden {
  display: none;
}
